import React, { useState, useRef } from "react"
import Button from "./Button"
import SectionHeader from './SectionHeader'
import { useForm } from '@formspree/react'; //ValidationError
import Loader from "react-loader-spinner";

export default function ContactForm() {
    const [name, setName] = useState("");
    const [email, setEmail] = useState("");
    const [msg, setMsg] = useState("");
    const [errors, setErrors] = useState(new Set());
    const [state, handleSubmit] = useForm("xpzkvozb");

    const formRef = useRef(null);
    const validateForm = () => {
        //check to see if there are any errors and fill the error set
        const newErrors = new Set();
        if (!name) {
            newErrors.add("name");
        }
        if (!email) {
            newErrors.add("email");
        }
        if (!msg) {
            newErrors.add("msg");
        }
        setErrors(newErrors);
        //return true if no errors found and subs form 
        return newErrors.size === 0;

    }
    const renderFeedback = (text, error = true) => {
        return <p
            className="contact-form__form__element__feedback"
            style={
                {
                    color: error ?
                        "#ED5565" :
                        "#609300",
                }
            }
        >{text}</p>
        return;
    }

    return (
        <section id="contact-me" className="section-spacing ">
            <SectionHeader title="Let's Talk" />
            <div className="contact-form">
                {/* Borders */}

                {/* Form */}
                <form
                    className="contact-form__form"
                    onSubmit={handleSubmit}
                    ref={formRef}
                    id="contact-form-form"
                >
                    <fieldset>
                        {/*<legend>Comment form</legend> */}
                        <div className="contact-form__form__element">
                            <label
                                className="contact-form__form__element__label">
                                Name
                            </label>
                            <input
                                value={name}
                                onChange={(e) => setName(e.target.value)}
                                className="contact-form__form__element__input"
                                type="text"
                                name="name"
                                id="name"
                                required
                            />
                            {errors.has("name") && renderFeedback("Enter your name")}
                        </div>

                        <div className="contact-form__form__element">
                            <label

                                className="contact-form__form__element__label">
                                Email
                            </label>
                            <input
                                value={email}
                                onChange={(e) => setEmail(e.target.value)}
                                className="contact-form__form__element__input"
                                type="text"
                                name="email"
                                id="email"
                                required
                            />
                            {errors.has("email") &&
                                renderFeedback("Enter your email")}
                            {state.errors.length > 0 && state.errors[0].code === "TYPE_EMAIL" &&
                                renderFeedback("Enter a valid email")}

                        </div>

                        <div className="contact-form__form__element">
                            <label className="contact-form__form__element__label">
                                Message
                            </label>
                            <textarea
                                value={msg}
                                onChange={(e) => setMsg(e.target.value)}
                                name="comment"
                                rows="8"
                                cols="40"
                                required
                            >
                            </textarea>
                            {errors.has("msg") &&
                                renderFeedback("Enter your message")}

                            {state.succeeded &&
                                renderFeedback("Thanks, I'll be in touch", false)
                            }
                            {state.submitting &&
                                <Loader
                                    type="ThreeDots"
                                    color="#4c8bf6"
                                    height={40}
                                    width={40}
                                    style={{ marginLeft: ".5em" }}
                                    visible={true}
                                />}
                        </div>

                    </fieldset>

                    <Button
                        containerStyle={{ margin: "auto", marginTop: 15 }}
                        text="Send It Over"
                        onPress={e => {
                            const formValid = validateForm();
                            if (formValid) formRef.current.dispatchEvent(
                                new Event("submit", { cancelable: true, bubbles: true })
                            )
                            return;
                        }}
                        disabled={state.submitting}
                    />
                </form>
            </div>
        </section>
    )
}
