import React from 'react'
import { Link } from 'gatsby'

export default function Navbar ({ navbarItems = [], isSticky = false }) {
  const [collapsed, setCollapsed] = React.useState(true)
  const renderHamburger = () => {
    return (
      <svg
        onClick={() => {
          setCollapsed(!collapsed)
        }}
        className='navbar__collpase__button'
        viewBox='0 0 100 80'
        width='20'
        height='20'
      >
        <rect width='100' height='20' rx='8'></rect>
        <rect y='35' width='100' height='20' rx='8'></rect>
        <rect y='70' width='100' height='20' rx='8'></rect>
      </svg>
    )
  }
  return (
    <nav
      style={{ position: isSticky ? 'sticky' : 'relative' }}
      className='navbar'
    >
      <span className='logo'>
        <Link
          activeStyle={{ color: 'var(--cream)' }}
          style={{ color: 'var(--cream)' }}
          to='/'
        >
          Portfolio
        </Link>
      </span>
      <div className='navbar__collpase'>
        {renderHamburger()}
        <ul
          className={'navbar__collpase__list ' + (collapsed ? 'collapsed' : '')}
        >
          {navbarItems.map((item, index) => {
            return (
              <Link
                className='navbar__collpase__list__link'
                to={item.link}
                key={index + '_navbar_item'}
              >
                {item.label}
              </Link>
            )
          })}
        </ul>
      </div>
    </nav>
  )
}
