import React from 'react'

export default function timeLineElement(
    {
        duration,
        position,
        company,
        description
    }
) {
    return (
        <div className="timeline-element">

            <div className="timeline-job-title lg">
                <span className="timeline-job-title__time">
                    {duration}
                </span>
                <span className="timeline-job-title__title">
                    {position}
                </span>
            </div>

            <div className="timeline-element__divider">
                <div
                    className="timeline-element-indicator"
                />

            </div>
            <div className="timeline-element__main-content">
                <div className="timeline-element__main-content__title-container">
                <p
                    className="timeline-element-title"
                >
                    {company}
                </p>
                <div className="timeline-job-title sm">
                <span className="timeline-job-title__time">
                    {duration}
                </span>
                <span className="timeline-job-title__title">
                    {position}
                </span>
            </div>
            </div>
                <p
                    className="timeline-element-description"
                >
                    {description}
                </p>
                <div className="timeline-element__bottom-padding" />

            </div>

        </div>
    )
}
