import React, { useState } from 'react'
import SectionHeader from './SectionHeader'
import Stack from './Stack'
import { graphql, useStaticQuery, Link } from 'gatsby'
import { GatsbyImage, getImage } from 'gatsby-plugin-image'
import Masonry from 'react-masonry-component'
const prjs = [
  {
      "id": 1,
      "label": "Mobile Applications",
      "active": true,
      "type": "dev",
      "projects": [
          {
              "id": 1,
              "image": "essemar.png",
              "name": "Essemar",
              "slug": "essemar",
              "stack": [
                  {
                      "title": "React Native",
                      "src": "images/react-native.svg"
                  },
                  {
                      "title": "JavaScript",
                      "src": "../images/javascript.png"
                  },
                  {
                      "title": "Redux",
                      "src": "../images/redux.png"
                  }
              ],
              "description": "An Ecommerce app for a groccer"
          },
          {
              "id": 24,
              "image": "vecostore.png",
              "name": "Vecostore",
              "slug": "vecostore",
              "stack": [
                  {
                      "title": "React Native",
                      "src": "images/react-native.svg"
                  },
                  {
                      "title": "JavaScript",
                      "src": "../images/javascript.png"
                  },
                  {
                      "title": "Redux",
                      "src": "../images/redux.png"
                  }
              ],
              "description": "An Ecommerce app for a pharmaceutical provider"
          }
      ]
  },
  {
      "id": 2,
      "label": "Landing Page",
      "active": false,
      "type": "dev",
      "projects": [
          {
              "id": 1,
              "image": "trydo.png",
              "name": "Trydo",
              "slug": "trydo",
              "stack": [
                  {
                      "title": "HTML",
                      "src": "images/html.png"
                  },
                  {
                      "title": "CSS",
                      "src": "images/css.png"
                  },
                  {
                      "title": "Sass",
                      "src": "images/sass.png"
                  },
                  {
                      "title": "Javascript",
                      "src": "images/react-native.svg"
                  }
              ],
              "description": "A dark themed landing page for digital agencies"
          },
          {
              "id": 2,
              "image": "mntn.png",
              "name": "MNTN",
              "slug": "mntn",
              "stack": [
                  {
                      "title": "HTML",
                      "src": "images/html.png"
                  },
                  {
                      "title": "CSS",
                      "src": "images/css.png"
                  },
                  {
                      "title": "Sass",
                      "src": "images/sass.png"
                  },
                  {
                      "title": "Javascript",
                      "src": "images/react-native.svg"
                  }
              ],
              "description": "A recreation of a figma design I found"
          }
      ]
  }
]

const masonryOptions = {
  transitionDuration: 500,
  fitWidth: true
}
const imagesLoadedOptions = { background: '.my-bg-image-el' }

export default function ProjectShowcase (props) {
  const data = useStaticQuery(graphql`
    query HeaderQuery {
      allFile(filter: { relativeDirectory: { eq: "thumbnails" } }) {
        edges {
          node {
            id
            childImageSharp {
              gatsbyImageData(
                layout: CONSTRAINED
                placeholder: BLURRED
                width: 1240
              )
            }
            base
          }
        }
      }
    }
  `)

  const [projectList, cProjectList] = useState(prjs)

  const [activeSubCategory, setActiveSubCategory] = useState(1) //id of activesubcate

  const changeActiveProjectType = id => {

    const newProjectList = projectList.map(item => {
      //if you click an active item
      //this make sures it stays active
      if (item.id === id) {
        setActiveSubCategory(item.id)
        return { ...item, active: true }
      }
      if (item.active) {
        return { ...item, active: false }
      }
      return item
    })
    cProjectList(newProjectList)
    return
  }
  const getActiveProjects = () => {
    const activeItem = projectList.filter(item => {
      if (item.id === activeSubCategory) return item
    })
    return activeItem[0].projects
  }

  function renderImage (image, name, slug, description, stack, index) {
    return (
      <Link key={'project_' + index} className='link' to={'projects/' + slug}>
        <article className='project-showcase__project'>
          <div className='project-showcase__project_image-con'>
            <GatsbyImage image={image} alt={'project thumbnail ' + slug} />
          </div>
          {/* the element that appears on hover */}
          <div className='project-showcase__project__on-hover'>
            <div className='project-showcase__project_on-hover__text-content'>
              <span>{name + ': '}</span>
              <p>{description}</p>
            </div>
            <div>
              {stack.map((item, index) => {
                return (
                  <Stack
                    title={item.title}
                    src={item.src}
                    key={index + '_stack_' + item.title}
                  />
                )
              })}
            </div>
          </div>
        </article>
      </Link>
    )
  }
  function getGatsbyImageData (name) {
    for (const item of data.allFile.edges) {
      if (item.node.base === name) return getImage(item.node)
    }
  }
  return (
    <section id='portfolio' className='section-spacing project-showcase'>
      <SectionHeader title='Project Showcase' />
      <div className='project-showcase__controls'>
        <ul className='project-showcase__controls__list'>
          {projectList.map((item, index) => {
            return (
              <li
                key={'project_list_ ' + item.label + '_' + index}
                onClick={() => changeActiveProjectType(item.id)}
                className={item.active ? 'active' : ''}
              >
                {item.label}
              </li>
            )
          })}
        </ul>
      </div>
      <div></div>
      <Masonry
        className={'project-showcase__image-group'} // default ''
        elementType={'div'} // default 'div'
        options={masonryOptions} // default {}
        disableImagesLoaded={false} // default false
        updateOnEachImageLoad={false} // default false and works only if disableImagesLoaded is false
        imagesLoadedOptions={imagesLoadedOptions} // default {}
      >
        {getActiveProjects()?.map((prjItem, index) => {
          const { image, name, description, stack, slug } = prjItem

          return renderImage(
            getImage(getGatsbyImageData(image)),
            name,
            slug,
            description,
            stack,
            index
          )
        })}
      </Masonry>
    </section>
  )
}
